import React from "react"
import Link from "gatsby-link"
import styled from "styled-components"
import Helmet from "react-helmet"
import Fade from "react-reveal/Fade"
import {
  TINY_SHADOW,
  TINY_BKG,
  SECONDARY_TEXT
} from "../../components/consts/colors.js"
import Icon from "../../components/Icons"
import "../../css/index.css"
import "../../css/typography.css"
import "../../css/Hide.css"
import SectionContainer from "../../components/SectionContainer"
import SectionContainerBackground from "../../components/SectionContainerBackground"
import SectionHeaderContent from "../../components/SectionHeaderContent"
import SectionContent from "../../components/SectionContent"
import {
  TinyButton,
  TinyButtonAlt,
  TinyButtonWhite
} from "../../components/Button.js"
import SectionImages from "../../components/SectionImages"
import SectionImage from "../../components/SectionImage"
import FAQHeader from "../../components/FAQHeader"
import fathom from "../../assets/Fathom.png"

import withScreensize from "../../components/withScreensize"
import ScreensizeProvider from "../../components/ScreensizeProvider"

const pageData = {
  FAQHeadline: "Easily upload your custom designs on selected products.",
  FAQCopy:
    "From your TinyMerch Dashboard you can easily add products to your store! First, select the type of product you wish to sell ( Currently there's the option of t-shirts or posters, with digital files and many other products coming soon!). After you've made your selection simply upload a file for the design. We recommend png or tiff as they tend to work the best. Now, you can fine tune the position of your file, as well as select some options such as size and color. Finally, name your price! Is it a $15 print? $50? $500? That's up to you! ( And what your target demographic can afford ðŸ˜œ )",
  FAQLink: "#",
  FAQwhatIsCTA: "Get Started!"
}

const NextQ = styled.div`
  margin: 10px 0px;
  a {
    display: flex;
    transition: all 200ms ease;
    :hover {
      transform: translateX(3px);
    }
    div {
      padding-left: 10px;
      position: relative;
      top: 2px;
    }
  }
`

const DesigningProds = withScreensize(props => {
  const isMobile = props.screensize === "phones"
  const isTablet = props.screensize === "thatUglyInBetweenSize"
  const isSmall = props.screensize === "devWithConsoleOpen"
  const isWidescreen = props.screensize === "widescreenFools"

  return (
    <div>
      <Helmet title="TinyMerch |Â FAQ â€“ What is TinyMerch" />
      <FAQHeader />
      <SectionContainer positionContent="center">
        <SectionContent
          justify="center"
          color
          height={
            isMobile ? (
              "300px"
            ) : isTablet ? (
              "400px"
            ) : isWidescreen ? (
              "500px"
            ) : (
              "500px"
            )
          }
          width={
            isMobile ? "95%" : isTablet ? "80%" : isWidescreen ? "40%" : "50%"
          }>
          <Fade left delay={250} duration={400}>
            <h2>{pageData.FAQHeadline}</h2>
          </Fade>
          <Fade left delay={300} duration={400}>
            <p style={{ paddingTop: "25px" }}>{pageData.FAQCopy}</p>
          </Fade>
          <Fade left delay={350} duration={400}>
            <NextQ>
              <Link to="/faq/getting-paid">
                ðŸ’¸ Getting Paid
                <Icon name="forward" color="#6079F2" />
              </Link>
            </NextQ>
          </Fade>
          {/* <a href={pageData.FAQLink}>
            <TinyButton>{pageData.FAQwhatIsCTA}</TinyButton>
          </a> */}
        </SectionContent>
        <SectionImages
          width={
            isMobile ? "100%" : isTablet ? "100%" : isWidescreen ? "60%" : "50%"
          }
          height={
            isMobile ? (
              "200px"
            ) : isTablet ? (
              "300px"
            ) : isWidescreen ? (
              "600px"
            ) : (
              "600px"
            )
          }>
          <Fade right delay={500} duration={750}>
            <SectionImage
              shadow
              src={fathom}
              ImgWidth={
                isMobile ? (
                  "300px"
                ) : isTablet ? (
                  "500px"
                ) : isWidescreen ? (
                  "700px"
                ) : (
                  "600px"
                )
              }
              positionTB={
                isMobile ? (
                  "10px"
                ) : isTablet ? (
                  "50px"
                ) : isWidescreen ? (
                  "80px"
                ) : (
                  "100px"
                )
              }
              positionLR={
                isMobile ? "0%" : isTablet ? "0%" : isWidescreen ? "20%" : "10%"
              }
            />
          </Fade>
        </SectionImages>
      </SectionContainer>
    </div>
  )
})

export default props => (
  <ScreensizeProvider>
    <DesigningProds {...props} />
  </ScreensizeProvider>
)



